import React from "react";
import PropTypes from "prop-types";
import {
  Switch,
  Route,
  Link,
  useLocation
} from "react-router-dom";
import {
  AppBar,
  CssBaseline,
  Drawer,
  Hidden,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Toolbar
} from "@material-ui/core";
import {
  Info,
  Menu,
  Work,
  School,
  Grade,
  Favorite,
  GitHub
} from "@material-ui/icons";
import { makeStyles, useTheme, createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { blue, grey, deepPurple, deepOrange } from "@material-ui/core/colors";
import DarkModeToggle from "react-dark-mode-toggle";
import {
  About,
  Experiences,
  Education,
  Skills,
  Interests,
  Github
} from "./page";
import "./styles/main.css";
import "./App.css";

const drawerWidth = 240;

function App(props) {
  const { wind } = props;
  const container = wind !== undefined ? () => wind().document.body : undefined;
  const prefersDark = window.matchMedia("(prefers-color-scheme: dark)").matches;
  const localDark = localStorage.getItem("dark");
  const isDark = localDark === null ? prefersDark : localDark === "true";
  let location = useLocation().pathname.replace("/", "");
  if (location === "") location = "about";
  const [state, setState] = React.useState({
    mobileOpen: false,
    page: location,
    darkState: isDark,
    repo: []
  });
  const { mobileOpen, page, darkState, repo } = state;

  const useStyles = makeStyles((theme) => ({
    root: {
      display: "flex"
    },
    drawer: {
      [theme.breakpoints.up("sm")]: {
        width: drawerWidth,
        flexShrink: 0
      }
    },
    appBar: {
      [theme.breakpoints.up("sm")]: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth
      }
    },
    menuButton: {
      marginRight: theme.spacing(2),
      [theme.breakpoints.up("sm")]: {
        display: "none"
      },
      backgroundColor: darkState ? grey[900] : blue[500]
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
      width: drawerWidth,
      color: "#ffffff",
      backgroundColor: darkState ? grey[900] : blue[500]
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3)
    }
  }));

  const palletType = darkState ? "dark" : "light";
  const mainPrimaryColor = darkState ? grey[900] : blue[500];
  const mainSecondaryColor = darkState ? deepOrange[900] : deepPurple[500];
  const darkTheme = createMuiTheme({
    palette: {
      type: palletType,
      primary: {
        main: mainPrimaryColor
      },
      secondary: {
        main: mainSecondaryColor
      }
    }
  });

  const classes = useStyles();
  const theme = useTheme();
  const handleDrawerToggle = () =>
    setState((prev) => ({ ...prev, mobileOpen: !mobileOpen }));
  const changePage = (page) => setState((prev) => ({ ...prev, page }));
  const handleThemeChange = React.useCallback(() => {
    localStorage.setItem("dark", !darkState);
    setState((prev) => ({ ...prev, darkState: !prev.darkState }));
  }, []);

  const menu = [
    { title: "About", url: "about", icon: <Info /> },
    { title: "Experiences", url: "experiences", icon: <Work /> },
    { title: "Education", url: "education", icon: <School /> },
    { title: "Skills", url: "skills", icon: <Grade /> },
    { title: "Interests", url: "interests", icon: <Favorite /> },
    {
      title: `Github Project (${repo.length})`,
      url: "github",
      icon: <GitHub />
    }
  ];

  React.useEffect(() => {
    Promise.all([
      fetch("https://api.github.com/users/gaetanozappi/repos?per_page=1000"),
      fetch(
        "https://api.github.com/users/TheCaponeBrothers/repos?per_page=1000"
      )
    ])
      .then(([resUser, resGroup]) =>
        Promise.all([resUser.json(), resGroup.json()])
      )
      .then(([dataUser, dataGroup]) =>
        setState((prev) => ({
          ...prev,
          repo: [...prev.repo, ...dataUser, ...dataGroup]
        }))
      )
      .catch((e) => {
        console.error("Error", e);
      });
  }, []);

  const drawer = (
    <div className="mt-32">
      <div className={classes.toolbar} />
      <List>
        {menu.map(({ title, url, icon }, index) => (
          <Link to={`/${url}`} key={title}>
            <ListItem button key={title} onClick={() => changePage(url)}>
              <ListItemIcon
                style={{ color: page === url ? "#ffffff" : "#ffffff80" }}
              >
                {icon}
              </ListItemIcon>
              <ListItemText
                primary={<span className="font-bold">{title}</span>}
                style={{ color: page === url ? "#ffffff" : "#ffffff80" }}
              />
            </ListItem>
          </Link>
        ))}
      </List>
    </div>
  );

  return (
    <ThemeProvider theme={darkTheme}>
      <div className={classes.root}>
        <CssBaseline />
        <AppBar
          position="fixed"
          className={classes.appBar}
          style={{
            backgroundColor: darkState ? "#303030" : grey[50],
            boxShadow: "none"
          }}
        >
          <Toolbar className={"shadow-none"}>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              className={classes.menuButton}
            >
              <Menu />
            </IconButton>
            <div className="flex-1 text-right flex" style={{}}>
              <DarkModeToggle
                onChange={handleThemeChange}
                checked={darkState}
                size={60}
              />
            </div>
          </Toolbar>
        </AppBar>

        <nav className={classes.drawer} aria-label="mailbox folders">
          {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
          <Hidden smUp implementation="css">
            <Drawer
              container={container}
              variant="temporary"
              anchor={theme.direction === "rtl" ? "right" : "left"}
              open={mobileOpen}
              onClose={handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper
              }}
              ModalProps={{
                keepMounted: true // Better open performance on mobile.
              }}
            >
              {drawer}
            </Drawer>
          </Hidden>
          <Hidden xsDown implementation="css">
            <Drawer
              classes={{
                paper: classes.drawerPaper
              }}
              variant="permanent"
              open
            >
              {drawer}
            </Drawer>
          </Hidden>
        </nav>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Switch>
            <Route path="/about">
              <About darkState={darkState} />
            </Route>
            <Route path="/experiences">
              <Experiences darkState={darkState} />
            </Route>
            <Route path="/education">
              <Education darkState={darkState} />
            </Route>
            <Route path="/skills">
              <Skills darkState={darkState} />
            </Route>
            <Route path="/interests">
              <Interests darkState={darkState} />
            </Route>
            <Route path="/github">
              <Github darkState={darkState} report={repo} />
            </Route>
            <Route path="/">
              <About darkState={darkState} />
            </Route>
          </Switch>
        </main>
      </div>
    </ThemeProvider>
  );
}

/*
{page === 0 && <About darkState={darkState} />}
{page === 1 && <Experiences darkState={darkState} />}
{page === 2 && <Education darkState={darkState} />}
{page === 3 && <Skills darkState={darkState} />}
{page === 4 && <Interests darkState={darkState} />}
{page === 5 && <Github darkState={darkState} report={repo} />}
*/

App.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  wind: PropTypes.func
};

export default App;
