import React from "react";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { className } from "../function";
import Var from "../variable";
import "../styles/main.css";

export default function Education({ darkState }) {
  const useStyles = makeStyles((theme) => ({
    title: {
      color: darkState ? "#ffffff" : "#343a40"
    }
  }));
  const classes = useStyles();
  const {
    educationPage: { title, educations }
  } = Var;
  return (
    <>
      <div className={className(classes.title, "text-5xl font-bold")}>
        {title}
      </div>
      {educations.map(({ type, start, end, title, description }, index) => (
        <div className="mt-8" key={title}>
          <div className={className(classes.title, "text-2xl font-bold")}>
            {type}
            <div className="sub-heading text-primary font-bold float-right">
              {start} - {end}
            </div>
          </div>
          <div className="sub-heading font-bold mb-4">{title}</div>
          <Typography paragraph>{description}</Typography>
        </div>
      ))}
    </>
  );
}
