import React from "react";
import { Tooltip } from "@material-ui/core";
import { Check } from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";
import { className } from "../function";
import Var from "../variable";
import "../styles/main.css";

export default function Skills({ darkState }) {
  const useStyles = makeStyles((theme) => ({
    title: {
      color: darkState ? "#ffffff" : "#343a40"
    }
  }));
  const classes = useStyles();
  const {
    skillsPage: { title, skills, specialist }
  } = Var;
  return (
    <>
      <div className={className(classes.title, "text-5xl font-bold mb-8")}>
        {title}
      </div>
      {skills.map(({ name, skill }) => (
        <div className="mt-8" key={name}>
          <div className="sub-heading font-bold mb-4">{name}</div>
          <ul className="list-inline list-icons">
            {skill.map(({ name, icon }) => (
              <li className="list-inline-item" key={name}>
                <Tooltip title={name} placement="bottom" arrow>
                  <i className={icon} original-title={name} />
                </Tooltip>
              </li>
            ))}
          </ul>
        </div>
      ))}
      <div className="sub-heading font-bold mt-4 mb-3">Specialist in</div>
      <ul>
        {specialist.map((el) => (
          <li key={el}>
            <Tooltip title={"Check"} placement="left" arrow>
              <Check className="cursor-pointer" style={{ color: "#2396f3" }} />
            </Tooltip>
            {el}
          </li>
        ))}
      </ul>
    </>
  );
}
